import React from 'react'
import { useFormik } from 'formik';
import axios from 'axios';


export default function Login() {

//////  ogject send data to backend
  async function allData(data){
   const res =await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup' ,data)


    console.log(data);
    
    


 }

    

//////  ogject send to backend
    let userData ={
        name: "",
        email:"",
        password:"",
        rePassword:"",
        phone:"",
    }

   const myForm= useFormik({
        initialValues : userData ,
        onSubmit :allData,
        validate:function(dataError){
            const error={}
            if(dataError.name.length<4||dataError.name.length>15){
                error.name = 'enter valid name'
            }
            if (dataError.email.includes('@')===false ||dataError.email.includes('.')===false  ) {
                error.email= 'Email IN Vaild'
                
                
              }
              if (! dataError.phone.match(/^01[0125][0-9]{8}$/)) {
                error.phone= 'phone IN Vaild'
                
              }
              if (dataError.password.length<6 ||dataError.password.length>12) {
                error.password= 'password must be from 6 and 12 characters'
               
              }
              if (dataError.rePassword!==dataError.password) {
                error.rePassword= 'password isnot match'
               
              }



             console.log('error' ,error);
            return error;
        }
           
        
    })





  return <>
<div className='container'>
  <div className='row'>
  <form  onSubmit={myForm.handleSubmit} >
   <div className="mb-3">
    <label htmlFor="name" className="form-label">name :</label>
    <input onChange={myForm.handleChange} value={myForm.values.name} onBlur={myForm.handleBlur} 
    type="text" className="form-control" id="name"/>
  </div>
  {myForm.errors.name&&myForm.touched.name?<div className=' alert alert-danger mt-2'>{myForm.errors.name}</div>:""}
  
  <div className="mb-3">
    <label htmlFor="email" className="form-label">Email :</label>
    <input onChange={myForm.handleChange} value={myForm.values.email} type='email' id='email' className="form-control"/>

  </div>
  <div className="mb-3">
    <label htmlFor="password" className="form-label">Password :</label>
    <input onChange={myForm.handleChange} value={myForm.values.password}  className="form-control" id="password"/>
   
  </div>
  <div className="mb-3">
    <label htmlFor="rePassword" className="form-label">RePassword :</label>
    <input  onChange={myForm.handleChange} value={myForm.values.rePassword}  type="Password" className="form-control" id="rePassword"/>
   
  </div>
  <div className="mb-3">
    <label htmlFor="phone" className="form-label">Phone :</label>
    <input onChange={myForm.handleChange}  value={myForm.values.phone}  type="tel" className="form-control" id="phone"/>
   
  </div>
  <button  type="submit" className="btn btn-success">
    Lodin
  </button>
</form>
  
  
  </div>

</div>
  </>
}
